/**
 * ÁΩëÁªúÁõëÊéßÊ†áÁ≠æÈ°µ
 * ËøΩË∏™ÂíåÂàÜÊûêÁΩëÁªúËØ∑Ê±Ç
 */

import type { Editor } from '../../core/Editor'
import { createLogger } from '../../utils/logger'

const logger = createLogger('NetworkTab')

export interface NetworkRequest {
  id: string
  url: string
  method: string
  status?: number
  statusText?: string
  headers?: Headers | Record<string, string>
  requestHeaders?: Headers | Record<string, string>
  requestBody?: any
  responseBody?: any
  startTime: number
  endTime?: number
  duration?: number
  size?: number
  type?: string
  error?: string
}

export class NetworkTab {
  private editor: Editor
  private container?: HTMLElement
  private requests: NetworkRequest[] = []
  private selectedRequest?: NetworkRequest
  private filter = ''
  private typeFilter = 'all'
  private isRecording = true
  private maxRequests = 500
  private requestsContainer?: HTMLElement
  private detailsContainer?: HTMLElement

  constructor(options: { editor: Editor }) {
    this.editor = options.editor
  }

  /**
   * Ê∏≤ÊüìÊ†áÁ≠æÈ°µ
   */
  render(): HTMLElement {
    this.container = document.createElement('div')
    this.container.className = 'network-tab'
    this.container.style.cssText = `
      display: flex;
      flex-direction: column;
      height: 100%;
    `

    // Â∑•ÂÖ∑Ê†è
    const toolbar = this.createToolbar()

    // ‰∏ªÂÜÖÂÆπÂå∫ÔºàÂàÜÂâ≤ËßÜÂõæÔºâ
    const content = this.createContent()

    this.container.appendChild(toolbar)
    this.container.appendChild(content)

    return this.container
  }

  /**
   * ÂàõÂª∫Â∑•ÂÖ∑Ê†è
   */
  private createToolbar(): HTMLElement {
    const toolbar = document.createElement('div')
    toolbar.className = 'network-toolbar'
    toolbar.style.cssText = `
      display: flex;
      align-items: center;
      padding: 8px;
      border-bottom: 1px solid #e0e0e0;
      background: #fafafa;
      gap: 10px;
      flex-shrink: 0;
    `

    // ÂΩïÂà∂ÊåâÈíÆ
    const recordBtn = document.createElement('button')
    recordBtn.style.cssText = `
      padding: 4px 8px;
      background: ${this.isRecording ? '#ff4444' : 'transparent'};
      color: ${this.isRecording ? 'white' : '#ff4444'};
      border: 1px solid #ff4444;
      border-radius: 4px;
      cursor: pointer;
      font-size: 12px;
    `
    recordBtn.innerHTML = this.isRecording ? '‚è∏ ÂÅúÊ≠¢' : '‚è∫ ÂΩïÂà∂'
    recordBtn.onclick = () => {
      this.isRecording = !this.isRecording
      recordBtn.style.background = this.isRecording ? '#ff4444' : 'transparent'
      recordBtn.style.color = this.isRecording ? 'white' : '#ff4444'
      recordBtn.innerHTML = this.isRecording ? '‚è∏ ÂÅúÊ≠¢' : '‚è∫ ÂΩïÂà∂'
    }

    // Ê∏ÖÁ©∫ÊåâÈíÆ
    const clearBtn = document.createElement('button')
    clearBtn.style.cssText = `
      padding: 4px 8px;
      background: transparent;
      border: 1px solid #666;
      border-radius: 4px;
      cursor: pointer;
      font-size: 12px;
    `
    clearBtn.innerHTML = 'üóëÔ∏è Ê∏ÖÁ©∫'
    clearBtn.onclick = () => this.clear()

    // ËøáÊª§Âô®
    const typeFilter = this.createTypeFilter()

    // ÊêúÁ¥¢Ê°Ü
    const searchBox = this.createSearchBox()

    // ÁªüËÆ°
    const stats = document.createElement('div')
    stats.className = 'network-stats'
    stats.style.cssText = `
      margin-left: auto;
      font-size: 12px;
      color: #666;
      display: flex;
      gap: 15px;
    `
    stats.innerHTML = `
      <span id="request-count">0 ËØ∑Ê±Ç</span>
      <span id="data-size">0 KB</span>
      <span id="load-time">0 ms</span>
    `

    toolbar.appendChild(recordBtn)
    toolbar.appendChild(clearBtn)
    toolbar.appendChild(typeFilter)
    toolbar.appendChild(searchBox)
    toolbar.appendChild(stats)

    return toolbar
  }

  /**
   * ÂàõÂª∫Á±ªÂûãËøáÊª§Âô®
   */
  private createTypeFilter(): HTMLElement {
    const container = document.createElement('select')
    container.style.cssText = `
      padding: 4px 8px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-size: 12px;
    `

    const types = [
      { value: 'all', label: 'ÂÖ®ÈÉ®' },
      { value: 'xhr', label: 'XHR' },
      { value: 'fetch', label: 'Fetch' },
      { value: 'script', label: 'JS' },
      { value: 'style', label: 'CSS' },
      { value: 'image', label: 'ÂõæÁâá' },
      { value: 'media', label: 'Â™í‰Ωì' },
      { value: 'font', label: 'Â≠ó‰Ωì' },
      { value: 'doc', label: 'ÊñáÊ°£' },
      { value: 'ws', label: 'WS' },
      { value: 'other', label: 'ÂÖ∂‰ªñ' },
    ]

    types.forEach((type) => {
      const option = document.createElement('option')
      option.value = type.value
      option.textContent = type.label
      container.appendChild(option)
    })

    container.onchange = (e) => {
      this.typeFilter = (e.target as HTMLSelectElement).value
      this.renderRequests()
    }

    return container
  }

  /**
   * ÂàõÂª∫ÊêúÁ¥¢Ê°Ü
   */
  private createSearchBox(): HTMLElement {
    const input = document.createElement('input')
    input.type = 'text'
    input.placeholder = 'ËøáÊª§ËØ∑Ê±Ç...'
    input.style.cssText = `
      padding: 4px 8px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-size: 12px;
      width: 200px;
    `

    input.oninput = (e) => {
      this.filter = (e.target as HTMLInputElement).value
      this.renderRequests()
    }

    return input
  }

  /**
   * ÂàõÂª∫‰∏ªÂÜÖÂÆπÂå∫
   */
  private createContent(): HTMLElement {
    const content = document.createElement('div')
    content.style.cssText = `
      flex: 1;
      display: flex;
      overflow: hidden;
    `

    // ËØ∑Ê±ÇÂàóË°®
    this.requestsContainer = document.createElement('div')
    this.requestsContainer.className = 'network-requests'
    this.requestsContainer.style.cssText = `
      flex: 1;
      overflow-y: auto;
      border-right: 1px solid #e0e0e0;
    `

    // ËØ¶ÊÉÖÈù¢Êùø
    this.detailsContainer = document.createElement('div')
    this.detailsContainer.className = 'network-details'
    this.detailsContainer.style.cssText = `
      width: 400px;
      overflow-y: auto;
      background: #fafafa;
      display: none;
    `

    // ËØ∑Ê±ÇË°®Ê†º
    this.requestsContainer.innerHTML = `
      <table style="width: 100%; border-collapse: collapse; font-size: 12px;">
        <thead style="background: #f5f5f5; position: sticky; top: 0;">
          <tr>
            <th style="padding: 8px; text-align: left; border-bottom: 2px solid #e0e0e0;">ÂêçÁß∞</th>
            <th style="padding: 8px; text-align: left; border-bottom: 2px solid #e0e0e0; width: 60px;">Áä∂ÊÄÅ</th>
            <th style="padding: 8px; text-align: left; border-bottom: 2px solid #e0e0e0; width: 60px;">Á±ªÂûã</th>
            <th style="padding: 8px; text-align: right; border-bottom: 2px solid #e0e0e0; width: 60px;">Â§ßÂ∞è</th>
            <th style="padding: 8px; text-align: right; border-bottom: 2px solid #e0e0e0; width: 60px;">Êó∂Èó¥</th>
          </tr>
        </thead>
        <tbody id="requests-tbody">
          <tr>
            <td colspan="5" style="padding: 20px; text-align: center; color: #999;">
              ÊöÇÊó†ÁΩëÁªúËØ∑Ê±Ç
            </td>
          </tr>
        </tbody>
      </table>
    `

    content.appendChild(this.requestsContainer)
    content.appendChild(this.detailsContainer)

    return content
  }

  /**
   * ÂºÄÂßãËÆ∞ÂΩïËØ∑Ê±Ç
   */
  startRequest(requestInfo: {
    url: string
    method: string
    headers?: Headers | Record<string, string>
    body?: any
  }): string {
    if (!this.isRecording)
      return ''

    const id = `req-${Date.now()}-${Math.random()}`
    const request: NetworkRequest = {
      id,
      url: requestInfo.url,
      method: requestInfo.method,
      requestHeaders: requestInfo.headers,
      requestBody: requestInfo.body,
      startTime: performance.now(),
      type: this.detectRequestType(requestInfo.url),
    }

    this.requests.push(request)

    // ÈôêÂà∂ËØ∑Ê±ÇÊï∞Èáè
    if (this.requests.length > this.maxRequests)
      this.requests.shift()

    this.addRequestRow(request)
    this.updateStats()

    return id
  }

  /**
   * ÂÆåÊàêËØ∑Ê±Ç
   */
  completeRequest(id: string, response: {
    status: number
    statusText: string
    headers?: Headers | Record<string, string>
    body?: any
    duration?: number
  }): void {
    const request = this.requests.find(r => r.id === id)
    if (!request)
      return

    request.status = response.status
    request.statusText = response.statusText
    request.endTime = performance.now()
    request.duration = response.duration || (request.endTime - request.startTime)
    request.responseBody = response.body

    // ‰º∞ÁÆóÂ§ßÂ∞è
    if (response.body)
      request.size = new Blob([JSON.stringify(response.body)]).size

    this.updateRequestRow(request)
    this.updateStats()
  }

  /**
   * Â§±Ë¥•ËØ∑Ê±Ç
   */
  failRequest(id: string, error: { error: string, duration?: number }): void {
    const request = this.requests.find(r => r.id === id)
    if (!request)
      return

    request.error = error.error
    request.endTime = performance.now()
    request.duration = error.duration || (request.endTime - request.startTime)

    this.updateRequestRow(request)
    this.updateStats()
  }

  /**
   * Ê£ÄÊµãËØ∑Ê±ÇÁ±ªÂûã
   */
  private detectRequestType(url: string): string {
    const urlLower = url.toLowerCase()

    if (urlLower.includes('/api/'))
      return 'xhr'
    if (urlLower.endsWith('.js'))
      return 'script'
    if (urlLower.endsWith('.css'))
      return 'style'
    if (/\.(jpg|jpeg|png|gif|svg|webp|ico)/.test(urlLower))
      return 'image'
    if (/\.(mp4|webm|ogg|mp3|wav)/.test(urlLower))
      return 'media'
    if (/\.(woff|woff2|ttf|eot|otf)/.test(urlLower))
      return 'font'
    if (urlLower.endsWith('.html'))
      return 'doc'
    if (urlLower.startsWith('ws://') || urlLower.startsWith('wss://'))
      return 'ws'

    return 'other'
  }

  /**
   * Ê∑ªÂä†ËØ∑Ê±ÇË°å
   */
  private addRequestRow(request: NetworkRequest): void {
    const tbody = this.container?.querySelector('#requests-tbody')
    if (!tbody)
      return

    // Ê∏ÖÁ©∫Âç†‰ΩçÁ¨¶
    if (tbody.children.length === 1 && tbody.children[0].textContent?.includes('ÊöÇÊó†'))
      tbody.innerHTML = ''

    const row = document.createElement('tr')
    row.dataset.id = request.id
    row.style.cssText = `
      cursor: pointer;
      transition: background 0.2s;
    `

    row.innerHTML = `
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; max-width: 300px;">
        ${this.getRequestName(request.url)}
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0;">
        <span style="color: #999;">...</span>
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0;">
        ${request.type}
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; text-align: right;">
        -
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; text-align: right;">
        <span style="color: #999;">...</span>
      </td>
    `

    row.onclick = () => this.selectRequest(request)

    row.onmouseenter = () => {
      row.style.background = '#f5f5f5'
    }

    row.onmouseleave = () => {
      row.style.background = 'transparent'
    }

    tbody.appendChild(row)
  }

  /**
   * Êõ¥Êñ∞ËØ∑Ê±ÇË°å
   */
  private updateRequestRow(request: NetworkRequest): void {
    const row = this.container?.querySelector(`tr[data-id="${request.id}"]`)
    if (!row)
      return

    const statusColor = request.error
      ? '#f44336'
      : request.status && request.status >= 400
        ? '#f44336'
        : request.status && request.status >= 300
          ? '#ff9800'
          : '#4caf50'

    row.innerHTML = `
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; max-width: 300px;">
        ${this.getRequestName(request.url)}
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0;">
        <span style="color: ${statusColor}; font-weight: bold;">
          ${request.status || (request.error ? 'ERR' : '...')}
        </span>
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0;">
        ${request.type}
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; text-align: right;">
        ${request.size ? this.formatSize(request.size) : '-'}
      </td>
      <td style="padding: 6px 8px; border-bottom: 1px solid #f0f0f0; text-align: right;">
        ${request.duration ? `${request.duration.toFixed(0)}ms` : '...'}
      </td>
    `
  }

  /**
   * Ê∏≤ÊüìÊâÄÊúâËØ∑Ê±Ç
   */
  private renderRequests(): void {
    const tbody = this.container?.querySelector('#requests-tbody')
    if (!tbody)
      return

    const filteredRequests = this.requests.filter((req) => {
      if (this.typeFilter !== 'all' && req.type !== this.typeFilter)
        return false

      if (this.filter && !req.url.toLowerCase().includes(this.filter.toLowerCase()))
        return false

      return true
    })

    if (filteredRequests.length === 0) {
      tbody.innerHTML = `
        <tr>
          <td colspan="5" style="padding: 20px; text-align: center; color: #999;">
            ÊöÇÊó†ÂåπÈÖçÁöÑÁΩëÁªúËØ∑Ê±Ç
          </td>
        </tr>
      `
      return
    }

    tbody.innerHTML = ''
    filteredRequests.forEach((request) => {
      this.addRequestRow(request)
      if (request.endTime)
        this.updateRequestRow(request)
    })
  }

  /**
   * ÈÄâÊã©ËØ∑Ê±ÇÊòæÁ§∫ËØ¶ÊÉÖ
   */
  private selectRequest(request: NetworkRequest): void {
    this.selectedRequest = request

    if (!this.detailsContainer)
      return

    this.detailsContainer.style.display = 'block'
    this.detailsContainer.innerHTML = `
      <div style="padding: 20px;">
        <h3 style="margin: 0 0 15px 0; font-size: 14px;">ËØ∑Ê±ÇËØ¶ÊÉÖ</h3>
        
        <div style="margin-bottom: 20px;">
          <div style="font-size: 12px; color: #999; margin-bottom: 5px;">URL</div>
          <div style="font-size: 12px; word-break: break-all;">${request.url}</div>
        </div>
        
        <div style="margin-bottom: 20px;">
          <div style="font-size: 12px; color: #999; margin-bottom: 5px;">ÊñπÊ≥ï</div>
          <div style="font-size: 12px;">${request.method}</div>
        </div>
        
        ${request.status
          ? `
          <div style="margin-bottom: 20px;">
            <div style="font-size: 12px; color: #999; margin-bottom: 5px;">Áä∂ÊÄÅ</div>
            <div style="font-size: 12px;">${request.status} ${request.statusText || ''}</div>
          </div>
        `
          : ''}
        
        ${request.duration
          ? `
          <div style="margin-bottom: 20px;">
            <div style="font-size: 12px; color: #999; margin-bottom: 5px;">ËÄóÊó∂</div>
            <div style="font-size: 12px;">${request.duration.toFixed(2)}ms</div>
          </div>
        `
          : ''}
        
        ${request.size
          ? `
          <div style="margin-bottom: 20px;">
            <div style="font-size: 12px; color: #999; margin-bottom: 5px;">Â§ßÂ∞è</div>
            <div style="font-size: 12px;">${this.formatSize(request.size)}</div>
          </div>
        `
          : ''}
        
        ${request.error
          ? `
          <div style="margin-bottom: 20px;">
            <div style="font-size: 12px; color: #999; margin-bottom: 5px;">ÈîôËØØ</div>
            <div style="font-size: 12px; color: #f44336;">${request.error}</div>
          </div>
        `
          : ''}
        
        <button onclick="this.parentElement.parentElement.style.display='none'" style="
          padding: 6px 12px;
          background: #666;
          color: white;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          font-size: 12px;
        ">ÂÖ≥Èó≠</button>
      </div>
    `
  }

  /**
   * Ëé∑ÂèñËØ∑Ê±ÇÂêçÁß∞
   */
  private getRequestName(url: string): string {
    try {
      const urlObj = new URL(url)
      return urlObj.pathname.split('/').pop() || urlObj.pathname
    }
    catch {
      return url.split('/').pop() || url
    }
  }

  /**
   * Ê†ºÂºèÂåñÂ§ßÂ∞è
   */
  private formatSize(bytes: number): string {
    if (bytes < 1024)
      return `${bytes}B`
    if (bytes < 1024 * 1024)
      return `${(bytes / 1024).toFixed(1)}KB`
    return `${(bytes / 1024 / 1024).toFixed(1)}MB`
  }

  /**
   * Êõ¥Êñ∞ÁªüËÆ°
   */
  private updateStats(): void {
    const count = this.requests.length
    const totalSize = this.requests.reduce((sum, req) => sum + (req.size || 0), 0)
    const totalTime = Math.max(...this.requests.map(req => req.endTime || req.startTime))
      - Math.min(...this.requests.map(req => req.startTime))

    const countEl = this.container?.querySelector('#request-count')
    if (countEl)
      countEl.textContent = `${count} ËØ∑Ê±Ç`

    const sizeEl = this.container?.querySelector('#data-size')
    if (sizeEl)
      sizeEl.textContent = this.formatSize(totalSize)

    const timeEl = this.container?.querySelector('#load-time')
    if (timeEl)
      timeEl.textContent = `${totalTime.toFixed(0)}ms`
  }

  /**
   * Ê∏ÖÁ©∫ËØ∑Ê±Ç
   */
  clear(): void {
    this.requests = []
    const tbody = this.container?.querySelector('#requests-tbody')
    if (tbody) {
      tbody.innerHTML = `
        <tr>
          <td colspan="5" style="padding: 20px; text-align: center; color: #999;">
            ÊöÇÊó†ÁΩëÁªúËØ∑Ê±Ç
          </td>
        </tr>
      `
    }
    this.updateStats()

    if (this.detailsContainer)
      this.detailsContainer.style.display = 'none'
  }

  /**
   * ÊøÄÊ¥ªÊ†áÁ≠æÈ°µ
   */
  activate(): void {
    // Ê†áÁ≠æÈ°µÊøÄÊ¥ª
  }

  /**
   * ÂÅúÁî®Ê†áÁ≠æÈ°µ
   */
  deactivate(): void {
    // Ê†áÁ≠æÈ°µÂÅúÁî®
  }

  /**
   * ÈîÄÊØÅ
   */
  destroy(): void {
    this.container = undefined
  }
}
